import styled, { keyframes } from 'styled-components';
import { shade } from 'polished';

export const Container = styled.div`

  > header {
    height: 144px;
    background: #28262e;
    display: flex;
    align-items: center;
    div {
      width: 100%;
      max-width: 1120px;
      margin: 0 auto;

      svg {
        color: #999591;
      }
    }
  }
`;

export const Content = styled.div`
  display: flex;
  flex-direction: column;
  place-content: center;
  width: 100%;
  margin: -175px auto 0 ;
  align-items: center;
  form {
    margin: 80px 0px;
    text-align: center;
    width: 340px;
    display: flex;
    flex-direction: column;
  }
  h1{ 
    margin-bottom: 24px;
    text-align: left;
    font-size: 16px;
  }

  a {
    display: block;
    color: #F4EDE8;
    text-decoration: none;
    transition: color 0.2s;
    margin-top: 24px;
    transition: 0.5s;

    &:hover {
      color: ${shade(0.2, '#F4EDE8')};
    }
  }

  > a {
    display: flex;
    align-items: center;
    color: #ff9000;
    text-decoration: none;
    transition: color 0.2s;
    margin-top: 24px;

    &:hover {
      color: ${shade(0.2, '#ff9000')};
    }

    svg {
      margin-right: 16px;
    }
  }
`;

export const AvatarInput = styled.div`
  margin-bottom: 32px;
  position: relative;
  align-self: center;
  img{
    width: 186px;
    height: 186px;
    border-radius: 50%;
  }
  label {
    position: absolute;
    width: 48px;
    height: 48px;
    background: #ff9000;
    border-radius: 50%;
    right: 0;
    bottom: 0;
    border: 0;
    transition: 0.2s;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    input {
      display: none;
    }
    &:hover{
      background: ${shade(0.2, '#ff9000')};
    }
  }
`;
