import styled, { css } from 'styled-components';
import Toottip from '../Tooltip';

interface ContainerProps {
  isFocused: boolean;
  isFilled: boolean;
  isErrored: boolean;
}

export const Container = styled.div<ContainerProps>`
    background: #232129;
    border-radius: 10px;
    border: 2px solid #232129;
    padding: 16px;
    width: 100%;
    color: rgba(244, 237, 232, 0.6);

    display: flex;
    align-items: center;


    & + div {
      margin-top: 8px;
    }
    ${props => props.isErrored && css`
    border-color: #c53030;
    `}

    ${props => props.isFocused && css`
    border-color: #ff9000;
    color: #ff9000;
    `}

    ${props => props.isFilled && css`
    color: #ff9000;
    `}

  input {
    appearance: none;
    flex: 1;
    background: transparent;
    border: 0;
    color: #F4EDE8;


    &::placeholder {
      color: #666360;
    }
  }
    svg {
      margin-right: 16px;
    }
`;
export const Error = styled(Toottip)`
  height: 20px;
  margin-left: 8px;

  svg {
    margin: 0;
  }
  span {
    background: #c53030;
    color: #fff;

    &::before {
      border-color: #c53030 transparent;
    }
  }
`;